"use client"

import { useState, useEffect } from "react"

export function Home() {
  const [scrollY, setScrollY] = useState(0)

  useEffect(() => {
    const handleScroll = () => {
      setScrollY(window.scrollY)
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  return (
    <section
      className="relative w-full h-screen flex items-center justify-center pt-16 overflow-hidden"
      style={{
        background: "linear-gradient(135deg, #f5f5f5 0%, #ffffff 100%)",
      }}
    >
      {/* Background with Parallax Effect */}
      <div
        className="absolute inset-0"
        style={{
          background: "url(/placeholder.svg?height=1080&width=1920&query=corporate-office-abstract)",
          backgroundSize: "cover",
          backgroundPosition: "center",
          opacity: 0.3,
          transform: `translateY(${scrollY * 0.5}px)`,
          transition: "transform 0.1s ease-out",
        }}
      />

      {/* Dark Overlay with Parallax */}
      <div
        className="absolute inset-0"
        style={{
          backgroundColor: "rgba(111, 111, 113, 0.15)",
          transform: `translateY(${scrollY * 0.3}px)`,
        }}
      />

      {/* Content with Counter-Parallax */}
      <div
        className="relative z-10 text-center px-6 max-w-4xl"
        style={{
          transform: `translateY(${scrollY * -0.2}px)`,
        }}
      >
        <h1 className="text-5xl md:text-7xl font-bold leading-tight mb-6 text-balance" style={{ color: "#1a1a1a" }}>
          A firm built on <span style={{ color: "#DC9D14" }}>TRUST</span>
        </h1>
        <p className="text-lg md:text-xl mb-8 text-balance" style={{ color: "#6F6F71" }}>
          Premium corporate solutions tailored for your business needs
        </p>
        <button
          className="px-8 py-3 rounded-md text-white font-semibold transition-all hover:shadow-lg hover:scale-105 md:text-lg"
          style={{ backgroundColor: "#DC9D14" }}
        >
          Get Started
        </button>
      </div>
    </section>
  )
}
